body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: -webkit-linear-gradient(left, #16699e, #17a140);
  background-image: linear-gradient(to right, #16699e, #17a140);
  color: white;
  height: 150px;
  font-size: 50px;
  
}

form {
  display: flex;
  justify-content: space-around;
  height: 50px
}

input {
  flex: 1 1 95%;
  margin: 0 10px;
  padding-left: 10px;
  outline: none;
}

a {
  margin: 0 10px 0 5px;
  flex: 1 0 5%;
  display: flex;
  justify-content: center;
  align-content: center;
  color: white;
  display: inline-block;
  /* background: linear-gradient(to right, #17a100, #17a140); */  
  background: -webkit-linear-gradient(left, #17a100, #16699e, #17a140);  
  background: linear-gradient(to right, #17a100, #16699e, #17a140);
  background-size: 400%;
}

a:hover {
  -webkit-animation: animate 4s linear infinite;
          animation: animate 4s linear infinite;
}

@-webkit-keyframes animate {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 400%;
  }
}

@keyframes animate {
  0% {
    background-position: 0%;
  }
  100% {
    background-position: 400%;
  }
}

.icon-flipped {
  padding-top: 10px;
  font-size: 30px;
  transform: scaleX(-1);
  -moz-transform: scaleX(-1);
  -webkit-transform: scaleX(-1);
  -ms-transform: scaleX(-1);
}

.results {
    height: 40vh;
    overflow: auto;
}

::-webkit-scrollbar { 
    display: none; 
}

tr {
    display: flex;
    align-items: baseline;
}
td {
    text-align: left;
    padding-left: 10px;
}

li {
    margin-bottom: 5px;
}

.starIcon {
    font-size: 20px;
    cursor: pointer;
}

.title {
    flex: 1 1 40%;
}

.description {
    flex: 1 1 60%;
    padding-right: 20px;
}
.favorites {
    background-color: #F2FAF1;
    height: 40vh;
    overflow: auto;
}

.favoritesTitle { 
    color: green;
    font-size: 36px;
    font-weight:500;
    letter-spacing: -2px;
    padding-left: 10px;
    text-align: left;
}

.favorites .starIcon {
    fill: green;
}
